.user-widget__dropdown {
  user-select: none;
  cursor: pointer;
}

.user-widget__dropdown__label {
  display: flex;
  padding: 10px 5px;
  text-transform: capitalize;
  justify-content: space-between;
  align-items: center;
  min-width: 85px;
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: none;
  font-size: inherit;
}

.user-widget__dropdown__label img {
  margin-left: 5px;
}

.user-widget__dropdown__menu {
  display: none;
}

.user-widget__dropdown__menu--open {
  padding: 10px 5px;
  display: block;
  background-color: white;
  color: rgba(0,0,0,.6);
  user-select: none;
  cursor: pointer;
  min-width: 85px;
}

.user-widget__dropdown__menu--open .option {
  background: transparent;
  display: block;
  border: none;
  font-size: inherit;
}

.user-widget__dropdown__menu--open:hover {
  color: rgba(0,0,0,1);
}

.user-widget__dropdown__menu__list_item {
  padding: 10px 5px;
  background: transparent;
  display: block;
  line-height: 1em;
  font-size: 12px;
  color: grey;
}

.user-widget__dropdown__menu__list_item:hover {
  color: #000;
}

.user-widget__dropdown__icon--open {
  transform: rotate(180deg);
}

@media (min-width: 768px) {
  .user-widget__dropdown__menu--open {
    position: absolute;
  }
}



