@import "dropdown.css";

#app {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.user-widget {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
  cursor: pointer;
  .user-widget-nav-button {
    display: flex;
    height: 100%;
    user-select: none;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    padding: 0 10px;
    background: transparent;
    border: none;
    font-size: inherit;
    text-decoration: none;
  }
  .dropdown-wrapper {
    position: relative;
    height: 40px;
    .user-widget__dropdown__menu {
      top: 100%;
    }
  }
}
.dropdown-wrapper {
  ul, li {
    padding: 0;
    margin: 0;
  }
  li {
    list-style: none;
  }
}
.user-widget__log-in {
  padding: 0 30px;
  margin: 5px 0;
  text-decoration: none;
  color: #fff;
}

.user-widget__free-trial {
  padding: 0 15px;
  background: #fff;
  border: none;
  border-radius: 4px;
  margin: 5px 0;
  color: #1f82e5;
  text-decoration: none;
  height: 34px;
  line-height: 34px;
  -moz-transition: background-color 0.25s,color 0.25s;
  -ms-transition: background-color 0.25s,color 0.25s;
  -o-transition: background-color 0.25s,color 0.25s;
  -webkit-transition: background-color 0.25s,color 0.25s;
  transition: background-color 0.25s,color 0.25s;

  &:hover {
    background-color: #28d8b2;
    color: #fff;
   }
}

.user-widget__projects_menu_toggle {
  margin-right: 10px;
  color: #fff;
  text-transform: capitalize;

  span + img {
    margin-left: 5px;
  }
}

.user-widget__tags {
  display: flex;
}

.user-widget__tag {
  padding: 10px;
  padding-right: 3px;

  span + span {
    margin-left: 10px;
  }
}

.user-widget__tags__input {
  &:focus{
    outline: none;
  }
}

.user-widget__tag__close {
  user-select: none;
  cursor: pointer;
}

.user-widget__search_input {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  background-color: rgba(255,255,255,.1);
  height: 36px;
  min-width: 200px;
  margin-bottom: 20px;

  input + img {
    margin-left: 10px;
    margin-top: 10px;
  }

  input {
    height: 34px;
  }
}

.user-widget__search_input__input {
  border: none;
  min-width: 150px;
  width: 80%;

  &:focus{
    outline: none;
  }
}

.user-widget__search_input__button {
  user-select: none;
  cursor: pointer;
  width: 24px;
  border: none;
  background: transparent;
  padding: 0;
}

.user-widget__mega_menu__search__filter_bar {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background-color: rgba(255,255,255,.1);
  color: #fff;
  min-height: 34px;

  img + span {
    margin-left: 5px;
  }

  span + div {
    margin-left: 5px;
  }
}

.user-widget__collapsible {
  margin-bottom: 2px;
}
.user-widget__collapsible__top_bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(61, 59, 63, 0.95);
  color: rgba(255,255,255,.8);
  cursor: pointer;
  padding: 10px;
  user-select: none;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  width: 100%;
  box-sizing: border-box;
  border: none;
  text-align: left;
  &:hover,
  &:focus {
    color: rgba(255,255,255,1);
    background: rgba(42, 40, 43,1);
  }
}
.user-widget__collapsible__top_bar--active {
  color: rgba(255,255,255,1);
  background: rgba(42, 40, 43,1);
}

.user-widget__collapsible__content__item {
  align-items: center;
  padding: 5px 10px;
  margin-right: 10px;
}

.user-widget__collapsible__content {
  display: none;
  padding: 15px;
  font-size: 14px;
  ul, li {
    padding: 0;
    margin: 0;
  }
  li {
    list-style: none;
    margin-bottom: 5px;
  }
}

.user-widget__collapsible__content--open {
  background-color: rgba(255,255,255,.1);
  display: block;

  > div {
    display: block;
    width: 100%;
  }

  > div + div {
    margin-top: 10px;
  }

  img {
    cursor: pointer;
  }

  button {
    display: block;
    background: transparent;
    border: none;
    padding: 0 15px;
  }
  button + button {
    margin-top: 15px;
  }
  .name {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .id {
    white-space: pre-wrap;
    overflow: hidden;
  }
  .size {
    text-transform: capitalize;
  }
  .id,
  .size,
  .email {
    font-size: 12px;
  }
}

.user-widget__avatar {
  border-radius: 50%;
  height: 32px;
  width: 32px;
}

.user-widget__user_name {
  color: white;
  margin: 0px 5px;
}

.user-widget__toggle {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0px 10px;
  margin-left: 2px;
}

.user-widget__accounts_menu_toggle--active,
.user-widget__projects_menu_toggle--active,
.user-widget__toggle--active {
  background: rgba(61, 59, 63, 0.95);
}

.user-widget__mega_menu {
  right: 0;
  width: 100%;
  background: rgba(61, 59, 63, 0.95);
  color: #fff;
  line-height: 1em;
  padding-left: 0;
  padding-right: 0;

  input[type="text"] {
    height: 34px;
    line-height: 34px;
    background: transparent;
    color: #fff;

    &::placeholder {
      color: rgba(255,255,255,.8);
    }

    &:focus {
      color: #fff;
    }
  }

  a {
    color: rgba(255,255,255,1);
    text-decoration: none;
    display: block;
    &:hover {
      color: rgba(0,174,239,1);
    }
  }


  a:hover {
    color: rgba(0,174,239,1);
  }

  a:after {
    content:" »"
  }
}

.user-widget__mega_menu--close {
  display: none;
}

.user-widget__mega_menu__admin {
  padding-top: 20px;
  padding-bottom: 20px;
}

.user-widget__mega_menu__admin__context,
.user-widget__mega_menu__admin__links {
  width: 100%;
  > div {
    padding: 0 20px;
  }
}
.user-widget__mega_menu__admin--title {
  min-height: 36px;
  padding: 10px 0;
  line-height: 34px;
}
.user-widget__mega_menu__admin--info {
  > div {
    margin-bottom: 5px;
  }
}
.user-widget__collapsible__content .user-widget__mega_menu__admin--links {
  padding: 15px 0;
  li {
    margin-bottom: 5px;
  }
  a {
    display: block;
  }
}
.user-widget__mega_menu__admin--tips .user-widget__collapsible__content--open {
  position: absolute;
  background: rgba(0,0,0,.95);
  color: #fff;

  &:before {
    content: "";
    border: 10px solid #000;
    border-color: transparent transparent rgba(0, 0, 0, .95) transparent;
    height: 1px;
    width: 1px;
    position: absolute;
    left: 10px;
    top: -20px;
  }

  > div {
    display: flex;
  }
}
.user-widget__mega_menu__admin--tips .user-widget__collapsible__top_bar {
  text-transform: capitalize;
  font-weight: normal;
  font-size: 14px;
  display: flex;
  padding: 5px 10px;
}
.user-widget__mega_menu__admin--tips .user-widget__collapsible__content__item {
  color: #fff;
  font-family: Menlo,Monaco,Consolas,"Courier New",monospace;
  width: 160px;
  font-size: 12px;
  height: 26px;
  padding: 0 10px;
  background: rgba(255,255,255,.2);
  border: none;
}

.user-widget__mega_menu__projects {
  width: 100%;
  max-width: 55vw;
  background-color: rgba(61, 59, 63, 0.95);
  color: #fff;
  padding-bottom: 20px;
}

.user-widget__mega_menu__search__title_bar {
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.user-widget__mega_menu__search__title_bar__left {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  & > span + div {
    margin-left: 20px;
  }
}

.user-widget__filter_dropdown > div {
  background: #fff;
  padding: 5px;
  border-radius: 5px;
  color: #000;
  a {
    color: rgba(0,0,0,.6);
  }
  a:hover {
    color: rgba(0,0,0,1);
  }
}
.user-widget__filter_dropdown > div {
  border-radius: 5px 5px 0 0;
}
.user-widget__filter_dropdown > div.user-widget__dropdown__menu {
  border-radius: 0 0 5px 5px;
}

.user-widget__dropdown__menu__input {
  display: flex;

  input + div {
    margin-left: 10px;
  }
}

.user-widget__dropdown__menu__input__button {
  display: flex;
  user-select: none;
  cursor: pointer;
}

.user-widget__filter_selector {
  display: flex;
  align-items: center;
  width: 70%;
  height: 100%;
  flex-wrap: wrap;
  color: #414141;

  &>div + div {
    margin-left: 5px;
  }
}

.user-widget__filter_add_button {
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  background: #eee;
  padding: 0 5px;
  line-height: 20px;
  height: 20px;
}

.user-widget__filter_tag {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  font-size: 13px;
  padding: 3px 6px;
  border-radius: 5px;
  text-transform: capitalize;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;

  span + div {
    margin-left: 10px;
    margin-bottom: 2px;
    font-size: .9em;
    font-weight: bold;
  }
}

.user-widget__filter_tag__close_btn {
  cursor: pointer;
  user-select: none;
  text-transform:lowercase;
  font-size: 15px;
}

.user-widget__list {
  padding: 15px 20px;
  background: rgba(61, 59, 63, 0.95);
}

.user-widget__list__table {
  background-color: rgba(255,255,255,.1);
  color: #fff;
  padding: 10px;
  min-height: 200px;
  max-height: 300px;
  overflow-y: scroll;
}

.user-widget__list__label {
  color: #fff;
  font-size: 16px;
  margin-bottom: 15px;
}

.user-widget__list__line {
  display: flex;
  margin-bottom: 10px;
}

.user-widget__list__empty {
  text-align: center;
  line-height: 1em;
  padding: 50px 0;
  opacity: .5;
}

.user-widget__list__header {
  width: 30%;
  font-size: 12px;
  margin-right: 20px;
  font-weight: 600;
  text-transform: uppercase;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.user-widget__list__cell {
  width: 30%;
  margin-right: 20px;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.user-widget__list__cell--small{
  width: 25%;
}

.user-widget__copy_to_clipboard {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 16px;
  button {
    padding: 0;
  }
}

.user-widget__copy_to_clipboard__info {
  font-size: 10px;
  position:absolute;
  width: 340px;
  display: block;
  text-align: center;
  left: 20px;
  background: rgba(255,255,255,.95);
  height: 26px;
  line-height: 26px;
  border-radius: 5px;
  color: #000;
  font-weight: bold;
}

.user-widget__dropdown__menu--projects,
.user-widget__dropdown__menu--account {
  padding: 1px;
  background: rgba(61,59,63,0.95);
  width: 90vw;

  .user-widget__dropdown__menu__list_item {
    color: #fff;
    padding: 10px 10px 10px 20px;
    background: transparent;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    border: none;
    width: 100%;
    box-sizing: border-box;
    font-size: inherit;
    text-align: left;
    cursor: pointer;
    text-decoration: none;
    &:hover {
      background-color: #000;
    }
  }
  .user-widget__dropdown__menu__section {
    display: block;
    padding: 15px 10px 5px;
    color: #b6b5b5;
    font-size: 11px;
    text-transform: uppercase;
  }
}

.user-widget__dropdown__menu--projects {
  min-width: 180px;
  max-width: 220px;
  max-height: 370px;
  overflow-y: scroll;
}

@media (max-width: 992px) {
  .user-widget__mega_menu__projects {
    max-width: 100vw;
  }
}

@media (min-width: 768px) {
  .user-widget__dropdown__menu--projects,
  .user-widget__dropdown__menu--account,
  .user-widget__mega_menu {
    position: absolute;
  }
  .user-widget__mega_menu__search__title_bar {
    display: flex;
    height: 55px;
  }
  .user-widget__mega_menu__search__title_bar__left,
  .user-widget__search_input {
    margin-bottom: 0;
  }
  .user-widget__search_input {
    width: 40%;
  }
  .user-widget__mega_menu__projects {
    width: 100%;
  }
  .user-widget__dropdown__menu--account {
    width: 180px;
  }
  .user-widget__mega_menu__admin {
    display: flex;
  }
  .user-widget__mega_menu__admin__context,
  .user-widget__mega_menu__admin__links {
    width: 50%;
  }
  .user-widget__mega_menu__admin--tips .user-widget__collapsible__content__item {
    width: 330px;
  }
}

@media (min-width: 992px) {
  .user-widget__mega_menu--open {
    display: flex;
  }
  .user-widget__mega_menu__admin {
    display: flex;
  }
  .user-widget__mega_menu__admin__context,
  .user-widget__mega_menu__admin__links {
    min-width: 230px;
  }
  .user-widget__mega_menu__admin__context > div {
    padding-right: 10px;
  }
  .user-widget__mega_menu__admin__links > div {
    padding-left: 10px;
  }
}

@media (min-width: 1200px) {
  .user-widget__mega_menu__admin__context,
  .user-widget__mega_menu__admin__links {
    min-width: 290px;
  }
}



